<template>
	<div class="dialogSettings">
		<Tabs :value="TabsType" @on-click="handTabsClick">
			<TabPane label="会话设置" name="name2">
				<div class="dialogSettings_top">
					<div class="dialogSettings_title"></div>
					<!-- <div class="dialogSettings_but">
						<Button type="primary" class="dialogSettings_but_one" @click="dialogueModal = true">添加对话包</Button>
					</div> -->
				</div>
				<div class="dataAnalysis_bot">
					<Table :columns="columns2" :data="dialogueList" ref="table" :height="tableHeight">
						<template slot-scope="{ row }" slot="switch">
							<i-switch :value="Boolean(row.state)" @on-change="handSetdialogue($event,row)"></i-switch>
							<!-- <div>{{row.state}}</div> -->
						</template>
						<template slot-scope="{ row }" slot="action">
							<Button type="primary" size="small" style="margin-right: 5px"
								@click="handUpDialoguePackModal(row)">修改名称</Button>
							<Button type="primary" size="small" style="margin-right: 5px"
								@click="handSetdialogueModal(row)">查看</Button>
							<Button type="warning" size="small" style="margin-right: 5px"
								@click="handdelDialogue(row)">删除</Button>
						</template>
					</Table>
				</div>
				<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
					<Page :total="total" :page-size="limit" show-total show-elevator
						@on-page-size-change="handleSizeChange" @on-change='handleCurrentChange' />
				</div>
			</TabPane>
			<TabPane label="自动追粉" name="name1">
				<div class="dialogSettings_top">
					<!-- <div class="dialogSettings_title"></div> -->
					<div class="flex-align-center">
						<div class="dialogSettings_lift">
							<div v-for="(item,index) in list" :key="index" :class="timeType == index ? 'color_blue' :''"
								class="dialogSettings_Lift_item cursor-pointer" @click="handtimeType(index)">
								{{item}}
							</div>
						</div>
						<div class="dialogSettings_Alert">
							<Alert v-if="timeType==0" banner closable type="warning">关注时发送指的是关注时立即发送</Alert>
							<Alert v-if="timeType==1" banner closable type="warning">未开口指的是关注用户尚未回复</Alert>
							<Alert v-if="timeType==2" banner closable type="warning">第一次开口后停滞指的是距离用户第一次会话消息间隔在30分钟以上</Alert>
							<Alert v-if="timeType==3" banner closable type="warning">第二次开口后停滞指的是距离用户第二次会话消息间隔在30分钟以上</Alert>
						</div>
						
					</div>
					
					<!-- <div class="dialogSettings_but">
						<Button type="primary" class="dialogSettings_but_one" @click="handPursuitModal">+新增话术</Button>
						<Button type="info" ghost>初始化</Button>
					</div> -->
				</div>
				
				
				<div class="dialogSettings_bot">
					<Table :columns="columns1" :data="pursuList" ref="table" :height="tableHeight">
						<template slot-scope="{ row }" slot="date">
							<div class="dialogSettings_bot_date">
								<span>{{row.gapTimeString}}</span>
								<Icon class="cursor-pointer" type="ios-create-outline" size='24' color="blue" @click="handUpGapTime(row)" />
							</div>
						</template>
						<template slot-scope="{ row,index }" slot="input">
							<div class="dialogSettings_bot_input">
								<div>
									<Input v-model="row.replyContent" style="width: 300px" type="textarea"
										@on-blur="handBlurContent(row,index)" :autosize="{minRows: 2,maxRows: 5}"
										placeholder="请输入内容" />
								</div>
								<!-- <Icon class="icon" type="md-happy" size='24' @click="show(index)" /> -->
								<!-- <Icon class="icon" type="ios-add-circle-outline" size='24' color="blue"
									@click="show(index)" /> -->
								<Icon  class="icon cursor-pointer" type="ios-checkmark-circle-outline" size='24' color="blue"
									@click="handoutline(row)" />
							</div>
						</template>
						<template slot-scope="{ row }" slot="timeType">
							<div>{{timeTypeList[row.timeType].label}}</div>
						</template>

						<template slot-scope="{ row }" slot="action">
							<Button size="small" style="margin-right: 5px"
								@click="handDelPursuitSetting(row)">删除</Button>
						</template>
					</Table>
				</div>
				<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
					<Page :total="total" :page-size="limit" show-total show-elevator
						@on-page-size-change="handleSizeChange" @on-change='handleCurrentChange' />
				</div>
				
			</TabPane>
			
			<TabPane label="异常信息" name="name3">
				<div class="dialogSettings_name3">
					<span>异常消息统一回复：
						<span v-if="setAbnormalFrom.replyContent.length">{{setAbnormalFrom.replyContent[0]}}</span>
						<span v-else>暂未设置</span>
						<span style="margin-left: 15px;" @click="handSetAbnormalModal">
							<Icon class="cursor-pointer" type="ios-create-outline" size="20" />
						</span>
					</span>
				</div>
				<div class="dataAnalysis_bot">
					<Table :columns="abnormalColumns" :data="abnormalList" ref="table" :height="tableHeight">
						<template slot-scope="{ row }" slot="portrait">
							<Avatar :src="row.portrait" />
							<!-- <img :src="row.portrait" width="50" height="50" /> -->
						</template>
					</Table>
				</div>
				<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
					<Page :total="total" :page-size="limit" show-total show-elevator
						@on-page-size-change="handleSizeChange" @on-change='handleCurrentChange' />
				</div>
			</TabPane>
			<TabPane label="客资设置" name="name4">
				<!-- <div class="dialogSettings_name3">
					<span>客资消息统一回复：
						<span v-if="getCustomerFrom.replyContent.length">{{getCustomerFrom.replyContent[0]}}</span>
						<span v-else>暂未设置</span>
						<span style="margin-left: 15px;" @click="handgetCustomerModal">
							<Icon type="ios-create-outline" size="20" />
						</span>
					</span>
				</div> -->
				<div class="mgtop_15px">
					<Form :label-width="100">
						<FormItem label="回复方式：">
							<RadioGroup v-model="getCustomerFrom.replyModel">
								<Radio label="1">随机回复一条</Radio>
								<Radio label="2">回复全部</Radio>
							</RadioGroup>
						</FormItem>
						<FormItem label="是否开启：">
							<RadioGroup v-model="getCustomerFrom.isOpen">
								<Radio label="1">是</Radio>
								<Radio label="0">否</Radio>
							</RadioGroup>
						</FormItem>
						<FormItem label="回复内容：">
							<div v-for="(item,index) in getCustomerFrom.replyContent" :key="index"
								class="flex-align-center mgbot_15px">
								<div style="width: 400px;">
									<Input type="textarea" :rows="3" v-model="getCustomerFrom.replyContent[index]"
										placeholder="请输入内容" />
								</div>
								<div class="mgleft_15 red cursor-pointer"  @click="handDeletegetCustomer(index)">
									<Icon type="ios-close-circle-outline" size="18" />
								</div>
							</div>
							<div @click="handgetCustomerPush" class="bule mgbot_15px cursor-pointer" style="width: 40px;">
								<Icon type="ios-add-circle-outline" size="18" />
							</div>
						</FormItem>
						<FormItem label="">
							<Button type="primary" @click="handsetCustomerReply">确定</Button>
						</FormItem>
					</Form>
				</div>
			</TabPane>
		<!-- <Button slot="extra" type="primary" class="dialogSettings_but_one" @click="dialogueModal = true">添加对话包</Button> -->
		<!-- <Button @click="handleTabsAdd" size="small" slot="extra">增加</Button> -->
		<div slot="extra" class="dialogSettings_but">
			<Button type="primary" v-if="TabsType=='name2'" class="dialogSettings_but_one" @click="dialogueModal = true">添加对话包</Button>
			<Button type="primary" v-if="TabsType=='name2'" class="mgleft_15" @click="handsynchronizeTemplate">同步系统默认对话模板</Button>
			<Button type="primary" v-if="TabsType=='name1'" class="dialogSettings_but_one" @click="handPursuitModal">+新增话术</Button>
		</div>
		</Tabs>

		<!-- 添加自动追粉 -->
		<Modal v-model="pursuitModal" title="添加话术" @on-ok="handSetPursuitSetting" @on-cancel="dialogueModal = false">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="时间：">
						<!-- <div class="mgbot_15px">
							<InputNumber :min="0" :max="31" v-model="pursuitFrom.day" controls-outside></InputNumber>
							<span class="mgleft_15">天</span>
						</div> -->
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="24" v-model="pursuitFrom.hour" controls-outside></InputNumber>
							<span class="mgleft_15">小时</span>
						</div>
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="60" v-model="pursuitFrom.minute" controls-outside></InputNumber>
							<span class="mgleft_15">分钟</span>
						</div>
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="60" v-model="pursuitFrom.second" controls-outside></InputNumber>
							<span class="mgleft_15">秒</span>
						</div>
					</FormItem>
					<FormItem label="时间类型：">
						<Select v-model="pursuitFrom.timeType" style="width:200px">
							<Option v-for="item in timeTypeList" :value="item.value" :key="item.value">{{ item.label }}
							</Option>
						</Select>
					</FormItem>
					<FormItem label="内容：">
						<div>
							<Input v-model="pursuitFrom.replyContent" style="width: 390px" type="textarea"
								:autosize="{minRows: 3,maxRows: 5}" placeholder="请输入内容" />
						</div>
					</FormItem>
				</Form>
			</div>
		</Modal>
		<Modal v-model="upGapTimeModal" title="修改时间" @on-ok="handSetPursuitSetting" @on-cancel="upGapTimeModal = false">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="时间：">
						<!-- <div class="mgbot_15px">
							<InputNumber :min="0" :max="31" v-model="pursuitFrom.day" controls-outside></InputNumber>
							<span class="mgleft_15">天</span>
						</div> -->
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="24" v-model="pursuitFrom.hour" controls-outside></InputNumber>
							<span class="mgleft_15">小时</span>
						</div>
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="60" v-model="pursuitFrom.minute" controls-outside></InputNumber>
							<span class="mgleft_15">分钟</span>
						</div>
						<div class="mgbot_15px">
							<InputNumber :min="0" :max="60" v-model="pursuitFrom.second" controls-outside></InputNumber>
							<span class="mgleft_15">秒</span>
						</div>
					</FormItem>
				</Form>
			</div>
		</Modal>

		<!-- 添加对话包 -->
		<Modal v-model="dialogueModal" title="添加对话包" @on-ok="handAdddialogue" @on-cancel="dialogueModal = false">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="对话包名称">
						<Input style="width: 350px;" v-model="dialogueValue" placeholder="请输入对话包名称" />
					</FormItem>
					<FormItem label="启用">
						<i-switch v-model="dialogueSwitch1"></i-switch>
					</FormItem>
				</Form>
			</div>
		</Modal>

		<Modal v-model="UpDialoguePackModal" title="修改对话包名称" @on-ok="handUpDialoguePack"
			@on-cancel="UpDialoguePackModal = false">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="对话包名称">
						<Input style="width: 350px;" v-model="dialogueValue" placeholder="请输入对话包名称" />
					</FormItem>
				</Form>
			</div>
		</Modal>

		<Modal v-model="setdialogueModal" width="1200" title="对话包详情" @on-ok="dialogueModal = false"
			@on-cancel="dialogueModal = false">
			<div class="mgtop_15px mgright_15" style="text-align: right;">
				<Button type="primary" size="large" style="margin-right: 5px;" @click="handAddSpeechArt">添加话术</Button>
			</div>
			<div class="mgtop_15px">
				<Table border :columns="speechArtColumns" :data="speechArt" height="550">
					<template slot-scope="{ row }" slot="action">
						<Button type="primary" size="small" style="margin-right: 5px"
							@click="handGetVerbalTrick(row)">修改</Button>
						<Button type="warning" size="small" style="margin-right: 5px"
							@click="handDelVerbalTrick(row)">删除</Button>

					</template>
					<template slot-scope="{ row }" slot="replySetting">
						<div v-if="row.replySetting==1">自动回复</div>
						<div v-if="row.replySetting==2">回复后关闭自动追粉</div>
						<div v-if="row.replySetting==3">回复后关闭自动回复</div>
						<div v-if="row.replySetting==4">回复后同时停止自动追粉和自动回复</div>
					</template>
					<template slot-scope="{ row }" slot="replyMode">
						<div>{{row.replyMode==1 ?'随机回复':'回复全部'}}</div>
					</template>
					<template slot-scope="{ row }" slot="replyContent">
						<div v-for="(item,index) in row.replyContent" :key="index">
							{{index+1}}：{{item}}
						</div>
					</template>
				</Table>
			</div>
			<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
				<Page :total="totalModal" :page-size="limitModal" show-total show-elevator
					@on-page-size-change="handleSizeModal" @on-change='handleCurrentModal' />
			</div>
		</Modal>


		<Modal v-model="speechArtFunModal" :title="this.speechArtType=='add' ? '添加话术' :'修改话术' " width="800"
			@on-ok="handAddVerbalTrick" @on-cancel="emptySpeechArtFrom">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="话术名称：">
						<Input style="width: 350px;" v-model="speechArtFrom.name" placeholder="请输入话术名称" />
					</FormItem>
					<FormItem label="回复设置：">
						<RadioGroup v-model="speechArtFrom.replyMode">
							<Radio label="1">随机回复</Radio>
							<Radio label="2">回复全部</Radio>
						</RadioGroup>
					</FormItem>
					<FormItem label="回复设置:">
						<RadioGroup v-model="speechArtFrom.replySetting">
							<Radio label="1">自动回复</Radio>
							<Radio label="2">回复后关闭自动追粉</Radio>
							<Radio label="3">回复后关闭自动回复</Radio>
							<Radio label="4">回复后同时停止自动追粉和自动回复</Radio>
						</RadioGroup>
					</FormItem>
					<FormItem label="内容：">
						<div v-for="(item,index) in speechArtFrom.replyContent" :key="index"
							class="flex-align-center mgbot_15px">
							<div style="width: 400px;">
								<Input type="textarea" :rows="3" v-model="speechArtFrom.replyContent[index]"
									placeholder="请输入内容" />
							</div>
							<div class="mgleft_15 red" @click="handDelete(index)">
								<Icon type="ios-close-circle-outline" size="18" />
							</div>
						</div>
						<div @click="handReplyContentPush" class="bule mgbot_15px" style="width: 40px;">
							<Icon type="ios-add-circle-outline" size="18" />
						</div>
					</FormItem>
					<FormItem label="关键词：">
						<div v-for="(item,index) in speechArtFrom.dialogueKey" :key="index"
							class="flex-align-center mgbot_15px">
							<div style="width: 400px;">
								<Input v-model="speechArtFrom.dialogueKey[index].keyWord" placeholder="请输入关键词" />
							</div>
							<Select v-model="speechArtFrom.dialogueKey[index].isFullMatch" style="width:200px" @on-change="handisFullMatchClick($event,index)">
							       <Option v-for="item in isFullMatchList" :value="item.value" :key="item.value">{{ item.label }}</Option>
							</Select>
							<div class="mgleft_15 red" @click="handKeyDelete(item,index)">
								<Icon type="ios-close-circle-outline" size="18" />
							</div>
						</div>
						<div @click="handKeyPush" class="bule mgbot_15px" style="width: 40px;">
							<Icon type="ios-add-circle-outline" size="18" />
						</div>
					</FormItem>
				</Form>
			</div>
		</Modal>


		<Modal v-model="setAbnormalModal" title="设置自动回复" width="800" @on-ok="handsetAbnormalSetting"
			@on-cancel="emptySetAbnormalFrom">
			<div class="mgtop_15px">
				<Form :label-width="100">
					<FormItem label="回复方式：">
						<RadioGroup v-model="setAbnormalFrom.replyModel">
							<Radio label="1">随机回复一条</Radio>
							<Radio label="2">回复全部</Radio>
						</RadioGroup>
					</FormItem>
					<FormItem label="是否开启：">
						<RadioGroup v-model="setAbnormalFrom.isOpen">
							<Radio label="1">是</Radio>
							<Radio label="0">否</Radio>
						</RadioGroup>
					</FormItem>
					<FormItem label="回复内容：">
						<div v-for="(item,index) in setAbnormalFrom.replyContent" :key="index"
							class="flex-align-center mgbot_15px">
							<div style="width: 400px;">
								<Input type="textarea" :rows="3" v-model="setAbnormalFrom.replyContent[index]"
									placeholder="请输入回复内容" />
							</div>
							<div class="mgleft_15 red cursor-pointer" @click="handDeleteSetAbnormal(index)">
								<Icon type="ios-close-circle-outline" size="18" />
							</div>
						</div>
						<div @click="handSetAbnormalPush" class="bule mgbot_15px cursor-pointer" style="width: 40px;">
							<Icon type="ios-add-circle-outline" size="18" />
						</div>
					</FormItem>
				</Form>
			</div>
		</Modal>

	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';

	export default {
		data() {
			return {
				list: ['关注时发送', '未开口', '第一次开口后停滞', '第二次开口后停滞'],
				TabsType: 'name2',
				timeType: 0,
				columns1: [{
						title: '排序',
						width: 100,
						type: 'index',
						align: 'center'
					},
					{
						title: '时间',
						slot: 'date',
						align: 'center',
					},
					{
						title: '发送内容',
						slot: 'input',
						width: 500,
						align: 'center'
					},
					{
						title: '时间类型',
						slot: 'timeType',
						align: 'center',
					},
					{
						title: '操作',
						slot: 'action',
						align: 'center'
					},
				],

				pursuList: [],
				pursuitModal: false,
				pursuitFrom: {
					day: 0,
					hour: 0,
					minute: 0,
					second: 0,
					timeType: 0,
					replyContent: '',
					pursuitId: '',
					gapTime: '',
				},
				upGapTimeModal: false,
				timeTypeList: [{
						value: 0,
						label: '关注时发送'
					}, {
						value: 1,
						label: '未开口'
					},

					{
						value: 2,
						label: '第一次开口'
					},
					{
						value: 3,
						label: '第二次开口'
					},
				],



				columns2: [{
						title: '对话包名称',
						key: 'name',
						align: 'center',
					},
					{
						title: '创建时间',
						key: 'creationTime',
						align: 'center',
					},
					{
						title: '是否启用',
						slot: 'switch',
						align: 'center'
					},
					{
						title: '操作',
						slot: 'action',
						align: 'center'
					},
				],

				dialogueList: [], //对话包泪飙
				dialogueModal: false,
				dialogueValue: '',
				dialogueSwitch1: false,

				// 查看对话包--话术
				setdialogueModal: false,
				speechArtColumns: [{
						title: '话术名称',
						key: 'name',
						align: 'center',
					},
					{
						title: '创建时间',
						key: 'creationTime',
						align: 'center',
					},
					{
						title: '回复设置',
						slot: 'replySetting',
						align: 'center'
					},
					{
						title: '回复方式',
						slot: 'replyMode',
						align: 'center'
					},
					{
						title: '回复内容',
						slot: 'replyContent',
						align: 'center'
					},
					{
						title: '关键字',
						key: 'keyWords',
						align: 'center'
					},
					{
						title: '操作',
						slot: 'action',
						align: 'center'
					},
				],
				dialoguePackId: '',
				UpDialoguePackModal: false, //对话框名称修改弹窗
				speechArt: [],
				// 异常消息
				abnormalColumns: [{
						title: '排序',
						type: 'index',
						align: 'center'
					},
					{
						title: '头像',
						slot: 'portrait',
						align: 'center',
					},
					{
						title: '昵称',
						key: 'nickname',
						align: 'center',
					},
					{
						title: '最后互动时间',
						key: 'creationTime',
						align: 'center',
					},
					{
						title: '异常内容',
						key: 'content',
						align: 'center',
					},
				],
				abnormalList: [],
				setAbnormalModal: false,
				setAbnormalFrom: {
					replyContent: [],
					replyModel: '1',
					isOpen: '0'
				},

				// 客资回复设置
				getCustomerFrom: {
					replyContent: [],
					replyModel: '1',
					isOpen: '0'
				},


				// 第三层 添加和修改。
				speechArtFunModal: false,
				speechArtType: '',
				speechArtFrom: {
					name: '', // 名称
					replyMode: '1', //回复方式
					replySetting: '1', // 回复设置
					replyContent: [], //回复内容
					dialogueKey: [{
						keyId: '',
						keyWord: '',
						isFullMatch:0,//是否全匹配
					}]
				},
				isFullMatchList:[
					 {
						value: 0,
						label: '半匹配'
					},
					{
						value: 1,
						label: '全匹配'
					},
				],
				
				delkey: [],

				page: 1, //页码
				limit: 15, // 每页的数量,
				total: 0,

				pageModal: 1,
				limitModal: 10,
				totalModal: 0,
				wxAppId: localStorage.getItem('wxAppId'),
				tableHeight: 607,
			}
		},
		computed: {
			textCopy() {
				return this.text.split(',').reverse().join()
			}
		},

		mounted() {
			window.addEventListener('resize', this.getTableHeight);
			this.getTableHeight();
			
			// this.handGetPursuitSetting()
			this.handGetDialogueList()
		},
		methods: {
			handsynchronizeTemplate(){
					this.$Modal.confirm({
						title: '提示',
						content: '<p>您确定同步系统默认对话模板吗,同步系统默认对话模板会导致默认对话模板发生改变</p>',
						loading: true,
						onOk: () => {
							indexApiModules.synchronizeTemplate({
								wxAppId: this.wxAppId,
							}).then(res => {
								console.log(res)
								this.$Modal.remove();
								this.$Message.success('同步成功');
							})
						}
					});
			},
			getTableHeight(){
				console.log(this.$refs.table);
				if(!this.$refs.table){ return}
				this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 250;
			},
			// 点击了导航栏
			handTabsClick(name) {
				this.page = 1,
					this.limit = 10,
					this.total = 0
				this.TabsType = name
				if (name == 'name1') {
					this.handGetPursuitSetting()
				}
				if (name == 'name2') {
					this.handGetDialogueList()
				}
				if (name == 'name3') {
					this.handGetAbnormalMsgList()
					this.handgetAbnormalSetting()
				}
				if (name == 'name4') {
					this.handgetCustomerReply()
				}
			},
			// 对话包列表
			handGetPursuitSetting() {
				const data = {
					page: this.page,
					limit: this.limit,
					wxAppId: this.wxAppId,
					timeType: this.timeType
				}
				indexApiModules.getPursuitSetting(data).then(res => {
					this.pursuList = res.data.list

					this.pursuList.forEach(item => {
						if (item.gapTime < 60) {
							item.gapTimeString = item.gapTime + "秒"
						} else {
							var min_total = Math.floor(item.gapTime / 60); // 分钟
							var sec = Math.floor(item.gapTime % 60); // 余秒
							if (min_total < 60) {
								item.gapTimeString = min_total + "分钟" + sec + "秒";
							} else {
								var hour_total = Math.floor(min_total / 60); // 小时数
								var min = Math.floor(min_total % 60); // 余分钟
								if (hour_total < 24) {
									item.gapTimeString = hour_total + "小时" + min + "分钟" + sec + "秒";
								} else {
									var day_total = Math.floor(hour_total / 24); //天数
									var hour = Math.floor(hour_total % 24); //余小时
									item.gapTimeString = day_total + "天" + hour + "小时" + min + "分钟" + sec +
										"秒";
								}
							}
						}
					})
					console.log('this.pursuList', this.pursuList)
					this.total = res.data.pageData.count
				})
			},



			handtimeType(index) {
				this.timeType = index
				this.handGetPursuitSetting()
			},
			handPursuitModal() {
				this.pursuitModal = true
			},

			// 新增/修改
			handSetPursuitSetting() {
				// `${}天${}时${}分${}秒`
				this.pursuitFrom.gapTime = (this.pursuitFrom.day * 86400) + (this.pursuitFrom.hour * 3600) + (this
					.pursuitFrom.minute * 60) + (this.pursuitFrom.second * 1)
				this.pursuitFrom.wxAppId = this.wxAppId
				console.log(this.pursuitFrom)
				let data = this.pursuitFrom
				indexApiModules.setPursuitSetting(data).then(res => {
					console.log(res)
					this.$Message.success('保存成功');
					this.handGetPursuitSetting()
					this.pursuitFrom = {
						day: 0,
						hour: 0,
						minute: 0,
						second: 0,
						timeType: 0,
						replyContent: '',
						pursuitId: '',
						gapTime: ''
					}
				})
			},
			// 内容修改
			handoutline(row) {
				let data = row
				indexApiModules.setPursuitSetting(data).then(res => {
					console.log(res)
					this.$Message.success('修改成功');
					this.handGetPursuitSetting()
				})
			},
			// 失去焦点触发
			handBlurContent(row, index) {
				let a = row.replyContent
				let b = this.pursuList[index].replyContent
				if (a != b) {
					this.handoutline(row)
				}

			},

			handUpGapTime(row) {
				this.pursuitFrom = row
				if (row.gapTime < 60) {
					this.pursuitFrom.day = 0,
						this.pursuitFrom.hour = 0,
						this.pursuitFrom.minute = 0,
						this.pursuitFrom.second = row.gapTime
				} else {
					var min_total = Math.floor(row.gapTime / 60); // 分钟
					var sec = Math.floor(row.gapTime % 60); // 余秒
					if (min_total < 60) {
						this.pursuitFrom.day = 0,
							this.pursuitFrom.hour = 0,
							this.pursuitFrom.minute = min_total,
							this.pursuitFrom.second = sec
					} else {
						var hour_total = Math.floor(min_total / 60); // 小时数
						var min = Math.floor(min_total % 60); // 余分钟
						if (hour_total < 24) {
							this.pursuitFrom.day = 0,
								this.pursuitFrom.hour = hour_total,
								this.pursuitFrom.minute = min,
								this.pursuitFrom.second = sec

						} else {
							var day_total = Math.floor(hour_total / 24); //天数
							var hour = Math.floor(hour_total % 24); //余小时
							this.pursuitFrom.day = day_total,
								this.pursuitFrom.hour = hour,
								this.pursuitFrom.minute = min,
								this.pursuitFrom.second = sec
						}
					}
				}
				this.upGapTimeModal = true
				console.log(this.pursuitFrom)

			},
			handDelPursuitSetting(row) {
				indexApiModules.delPursuitSetting({
					pursuitId: row.pursuitId
				}).then(res => {
					console.log(res)
					this.$Message.success('删除成功');
					this.handGetPursuitSetting()
				})
			},





			// 对话包列表
			handGetDialogueList() {
				const data = {
					page: this.page,
					limit: this.limit,
					wxAppId: this.wxAppId,
				}
				indexApiModules.getDialogueList(data).then(res => {
					this.dialogueList = res.data.list
					this.total = res.data.pageData.count
				})
			},
			// 添加
			handAdddialogue() {
				const data = {
					"name": this.dialogueValue,
					"state": Number(this.dialogueSwitch1),
					"wxAppId": this.wxAppId
				}
				indexApiModules.adddialogue(data).then(res => {
					console.log(res)
					this.$Message.success('添加成功');
					this.dialogueValue = '',
						this.dialogueSwitch1 = false,
						this.handGetDialogueList()
				})
			},
			// 设置
			handSetdialogue(status, row) {
				console.log(status, row)
				const data = {
					"dialoguePackId": row.dialoguePackId,
					"name": row.name,
					"state": Number(status),
					"wxAppId": this.wxAppId
				}
				console.log(data)
				indexApiModules.setdialogue(data).then(res => {
					console.log(res)
					this.$Message.success('修改成功');
					this.handGetDialogueList()
				})
			},
			// 删除
			handdelDialogue(row) {
				const data = {
					"dialoguePackId": row.dialoguePackId,
				}
				console.log(data)
				indexApiModules.delDialogue(data).then(res => {
					console.log(res)
					this.$Message.success('删除成功');
					this.handGetDialogueList()
				})
			},
			handSetdialogueModal(row) {
				this.dialoguePackId = row.dialoguePackId
				this.getVerbalTrickList()
			},
			// 打开修改弹窗
			handUpDialoguePackModal(row) {
				this.dialoguePackId = row.dialoguePackId
				this.UpDialoguePackModal = true
			},
			handUpDialoguePack() {
				const data = {
					"dialoguePackId": this.dialoguePackId,
					'name': this.dialogueValue,
				}
				console.log(data)
				indexApiModules.updateDialoguePack(data).then(res => {
					console.log(res)
					this.UpDialoguePackModal = false
					this.$Message.success('修改成功');
					this.handGetDialogueList()
				})
			},
			// 获取话术列表
			getVerbalTrickList() {
				const data = {
					page: this.pageModal,
					limit: this.limitModal,
					dialoguePackId: this.dialoguePackId,
				}
				console.log(data)
				indexApiModules.getVerbalTrickList(data).then(res => {
					console.log(res)
					this.setdialogueModal = true
					this.speechArt = res.data.list
					this.totalModal = res.data.pageData.count
				})
			},
			handAddSpeechArt() {
				this.speechArtType = 'add'
				this.speechArtFunModal = true
			},
			// 添加术语
			handAddVerbalTrick() {
				console.log(this.speechArtFrom.dialogueKey);
				console.log('2',this.speechArtFrom);
				if (this.speechArtType == 'add') {
					let data = {
						dialoguePackId: this.dialoguePackId,
						replyMode: Number(this.speechArtFrom.replyMode),
						name: this.speechArtFrom.name,
						replySetting: Number(this.speechArtFrom.replySetting),
						replyContent: this.speechArtFrom.replyContent,
						dialogueKey: this.speechArtFrom.dialogueKey,
						wxAppId: this.wxAppId,
					}
					indexApiModules.addVerbalTrick(data).then(res => {
						console.log(res)
						this.getVerbalTrickList()
						this.emptySpeechArtFrom()
					})
				}
				if (this.speechArtType == 'up') {
					let data = this.speechArtFrom
					data.wxAppId = this.wxAppId
					data.replyMode = Number(this.speechArtFrom.replyMode),
						data.replySetting = Number(this.speechArtFrom.replySetting),
						data.delkey = this.delkey
					console.log(data)
					indexApiModules.updateVerbalTrick(data).then(res => {
						console.log(res)
						this.getVerbalTrickList()
						this.emptySpeechArtFrom()
					})
				}

			},
			// 修改 获取详情-回显示
			handGetVerbalTrick(row) {
				indexApiModules.getVerbalTrick({
					dialogueRoleId: row.dialogueRoleId
				}).then(res => {
					this.speechArtFrom = res.data
					this.speechArtFrom.replyMode = String(res.data.replyMode)
					this.speechArtFrom.replySetting = String(res.data.replySetting)
					this.speechArtFunModal = true
					this.speechArtType = 'up'
					console.log('speechArtFrom', this.speechArtFrom)
				})
			},
			// 删除话术
			handDelVerbalTrick(row) {
				indexApiModules.delVerbalTrick({
					dialogueRoleId: row.dialogueRoleId
				}).then(res => {
					console.log(res)
					this.$Message.success('删除成功');
					this.getVerbalTrickList()
				})
			},

			// 清空speechArtFrom 并关闭弹窗
			emptySpeechArtFrom() {
				this.speechArtFunModal = false
				this.speechArtFrom = {
					name: '', // 名称
					replyMode: '1', //回复方式
					replySetting: '1', // 回复设置
					replyContent: [], //回复内容
					dialogueKey: [{
						keyId: '',
						keyWord: '',
						isFullMatch:0,
					}]
				}
				this.delkey = []
			},
			// 添加内容
			handReplyContentPush() {
				this.speechArtFrom.replyContent.push('')
			},
			// 删除内容
			handDelete(index) {
				this.speechArtFrom.replyContent.splice(index, 1)
			},
			// 添加关键字
			handKeyPush() {
				this.speechArtFrom.dialogueKey.push({
					keyWord: '',
					keyId: "",
					isFullMatch:0,
				})
			},
			handisFullMatchClick(value,index){
				console.log(value,index);
				this.speechArtFrom.dialogueKey[index].isFullMatch = value
			},
			// 删除关键字
			handKeyDelete(row, index) {
				if (row.keyId) {
					this.delkey.push(row.keyId)
				}
				console.log(this.delkey)
				this.speechArtFrom.dialogueKey.splice(index, 1)
			},
			// 异常消息列表
			handGetAbnormalMsgList() {
				indexApiModules.getAbnormalMsgList({
					page: this.page,
					limit: this.limit,
				}).then(res => {
					this.abnormalList = res.data.list
					this.total = res.data.pageData.count
				})
			},
			//获取异常消息回复设置
			handgetAbnormalSetting() {
				indexApiModules.getAbnormalSetting({
					wxAppId: this.wxAppId
				}).then(res => {
					this.setAbnormalFrom = res.data
				})
			},
			// 点击了设置 自动回复
			handSetAbnormalModal() {
				this.setAbnormalModal = true
				this.setAbnormalFrom.replyModel = String(this.setAbnormalFrom.replyModel)
				this.setAbnormalFrom.isOpen = String(this.setAbnormalFrom.isOpen)
			},
			// 设置自动回复
			handsetAbnormalSetting() {
				let data = this.setAbnormalFrom
				console.log(this.setAbnormalFrom)
				data.replyModel = Number(this.setAbnormalFrom.replyModel)
				data.isOpen = Number(this.setAbnormalFrom.isOpen)
				data.wxAppId = this.wxAppId
				indexApiModules.setAbnormalSetting(data).then(res => {
					console.log(res)
					this.$Message.success('设置成功');
					this.handgetAbnormalSetting()
				})
			},
			handSetAbnormalPush() {
				this.setAbnormalFrom.replyContent.push('')
			},
			handDeleteSetAbnormal(index) {
				this.setAbnormalFrom.replyContent.splice(index, 1)
			},
			// 清空
			emptySetAbnormalFrom() {
				this.setAbnormalFrom = {
					replyContent: [],
					replyModel: '1',
					isOpen: '0'
				}
				this.handgetAbnormalSetting()
			},
			// 客资
			handgetCustomerReply() {
				indexApiModules.getCustomerReply({
					wxAppId: this.wxAppId
				}).then(res => {
					this.getCustomerFrom = res.data
					this.getCustomerFrom.replyModel = String(this.getCustomerFrom.replyModel)
					this.getCustomerFrom.isOpen = String(this.getCustomerFrom.isOpen)
				})
			},
			handgetCustomerPush() {
				this.getCustomerFrom.replyContent.push('')
			},
			handDeletegetCustomer(index) {
				this.getCustomerFrom.replyContent.splice(index, 1)
			},
			handsetCustomerReply() {
				let data = this.getCustomerFrom
				console.log(this.getCustomerFrom)
				data.replyModel = Number(this.getCustomerFrom.replyModel)
				data.isOpen = Number(this.getCustomerFrom.isOpen)
				data.wxAppId = this.wxAppId
				indexApiModules.setCustomerReply(data).then(res => {
					console.log(res)
					this.$Message.success('设置成功');
					this.handgetCustomerReply()
				})
			},
			//话术弹窗的当前页
			handleSizeModal(val) {
				this.pageModal = val
				this.handSetdialogueModal()
			},
			handleCurrentModal(val) {
				this.limitModal = val;
				this.pageModal = 1;
				this.handSetdialogueModal()
			},
			// 当前页
			handleCurrentChange(val) {
				this.page = val
				console.log(this.TabsType)
				if (this.TabsType == 'name1') {
					this.handGetPursuitSetting()
				}
				if (this.TabsType == 'name2') {
					this.handGetDialogueList()
				}
				if (this.TabsType == 'name3') {
					console.log(this.page)
					this.handGetAbnormalMsgList()
				}

			},
			//每页数
			handleSizeChange(val) {
				this.limit = val;
				this.page = 1;
				if (this.TabsType == 'name1') {
					this.handGetPursuitSetting()
				}
				if (this.TabsType == 'name2') {
					this.handGetDialogueList()
				}
				if (this.TabsType == 'name3') {
					this.handGetAbnormalMsgList()
				}
			},

			show(index) {
				console.log(index)
			},
		},
	};
</script> 

